"use client";
import {
  Dispatch,
  SetStateAction,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import Console from "@/utils/console";
import useGlobalContext, {
  myAccessType,
  navSearchBarType,
} from "../GlobalContext";
import { hideLoader } from "../BlankPageLoader";
import { useSWR } from "@/utils/api-handler/swr";
import { isNull } from "@/utils/more";
import SpinLoader from "@/components/preloader/SpinLoader";
import api from "@/app/api/endpoints";
import $$ from "@/utils/global-variables";
import Image from "next/image";
import { dateTimeFormatter } from "@/utils/date-time-formatter";
import axios from "@/utils/api-handler/axios";
import { decode } from "@/utils/text-encode-decode";
import SvgFlowerPot from "@/components/svg-image/FlowerPot";

export default function Page() {
  const { myAccess, setActiveTab, doRefetch, navSearchBar, setNavSearchBar } =
    useGlobalContext();
  return useMemo(
    () => (
      <_Page
        myAccess={myAccess}
        setActiveTab={setActiveTab}
        doRefetch={doRefetch}
        navSearchBar={navSearchBar}
        setNavSearchBar={setNavSearchBar}
      />
    ),
    [myAccess, setActiveTab, doRefetch, navSearchBar, setNavSearchBar]
  );
}

function _Page({
  myAccess,
  setActiveTab,
  doRefetch,
  navSearchBar,
  setNavSearchBar,
}: {
  myAccess: myAccessType;
  setActiveTab: Dispatch<SetStateAction<string>>;
  doRefetch: boolean;
  navSearchBar: navSearchBarType;
  setNavSearchBar: Dispatch<SetStateAction<navSearchBarType>>;
}) {
  Console.component("Page");

  useEffect(() => {
    document.title = "History";
    setActiveTab("history");
  }, [setActiveTab]);

  const [data, setData] = useState<
    | {
        id: number;
        user: { id: number; name: string; image: string };
        text: string;
        action: string;
        undoParams: string;
        createdAt: string;
      }[]
    | undefined
  >(undefined);
  const [haveMoreData, setHaveMoreData] = useState<boolean>(true);
  const [showRefetchLoader, setShowRefetchLoader] = useState<boolean>(false);
  const [limit, setLimit] = useState<number>(50);

  /*****
   * Get all users data
   ******/
  const [res, mutate] = useSWR(
    api.readAllHistory +
      "?limit=" +
      limit +
      "&&searchKey=" +
      navSearchBar.keyword.trim() +
      "&&mine=" +
      (myAccess?.viewOthersHistory ? 0 : 1)
  );
  useEffect(() => {
    if (!res) return;
    if (res.status === 200) {
      setData(res.data);
      setHaveMoreData(res.haveMoreData);
      setNavSearchBar((prev) => ({ ...prev, isLoading: false }));
      setShowRefetchLoader(false);
    }
    hideLoader();
  }, [res]);

  /*****
   * Reload data fetchers
   ******/
  useEffect(() => {
    if (doRefetch) mutate();
  }, [doRefetch, mutate]);

  /*****
   * Refetching record on scroll
   ******/
  const observerTarget = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0] && entries[0].isIntersecting) {
          if (haveMoreData) {
            setShowRefetchLoader(true);
            setLimit((prev) => prev + 50);
          }
        }
      },
      { threshold: 0.1 }
    );

    const el = observerTarget.current;
    if (haveMoreData && isNull(navSearchBar.keyword) && !showRefetchLoader) {
      if (el) observer.observe(el);
    } else {
      if (el) observer.unobserve(el);
    }

    return () => {
      if (el) observer.unobserve(el);
    };
  }, [
    observerTarget,
    data,
    navSearchBar.keyword,
    showRefetchLoader,
    setLimit,
    haveMoreData,
  ]);

  // Function: Is undo button shown?
  function remainingUndoTime(timeString: string) {
    const givenTime: any = new Date(timeString);
    const currentTime: any = new Date();
    const diffInMilliseconds = currentTime - givenTime;

    const diffInMinutes = Math.floor(diffInMilliseconds / 1000 / 60);
    const hours = Math.floor(diffInMinutes / 60);
    const minutes = diffInMinutes % 60;
    const h = 24 - hours;
    const m = 60 - minutes;
    if (h <= 0) return undefined;
    else if (m <= 0) return undefined;
    else return `${h}h`;
  }

  // Undo delete related --->
  const [undoingId, setUndoingId] = useState<string[]>([]);
  async function undoFunctions(encodedData: string, historyId: number) {
    setUndoingId((prev) => [...prev, encodedData]);
    const data = decode(encodedData);

    if (data.case === "delete user")
      await axios.delete(api.deleteUser + "undo/" + data.userId);
    else if (data.case === "delete faculty")
      await axios.delete(api.deleteFaculty + "undo/" + data.facultyId);
    else if (data.case === "delete department")
      await axios.delete(api.deleteDepartment + "undo/" + data.departmentId);
    else if (data.case === "delete program")
      await axios.delete(api.deleteProgram + "undo/" + data.programId);
    else if (data.case === "delete session")
      await axios.delete(api.deleteSession + "undo/" + data.sessionId);

    // Delete history
    const res = await axios.delete(api.deleteHistory + historyId);
    if (res.status === 200) await mutate();
    setUndoingId((prev) => prev.filter((id) => id !== encodedData));
  }
  // <--- Undo delete related

  return (
    <div className="w-full h-full px-8 py-6 bg-white dark:bgBlack2 rounded-lg">
      {/* Title */}
      <div className="flex items-center gap-2 mb-2 text-gray-600">
        <div className="fontGs text-2xl">History</div>
      </div>

      {/* All history data */}
      <div>
        {data !== undefined &&
          (data.length === 0 ? (
            <>
              {myAccess?.viewOthersHistory ? (
                <div className="w-fit mx-auto my-8">
                  <SvgFlowerPot size={130} />
                  <div className="mt-4 text-center fontGsm text-gray-500">
                    No history found
                  </div>
                </div>
              ) : (
                <div className="w-fit mx-auto my-8">
                  <SvgFlowerPot size={130} />
                  <div className="mt-4 text-center fontGsm text-gray-500">
                    You have no history
                  </div>
                </div>
              )}
            </>
          ) : (
            data.map((record, index) => {
              return (
                <div key={index} className="py-2">
                  <div className="mb-0.5 fontGsm">{record.text}</div>
                  <div className="flex items-center gap-2">
                    <Image
                      // src={record.user.image || $$.defaultUserImg}
                      src={$$.defaultUserImg}
                      className="rounded-full"
                      width="16"
                      height="16"
                      alt="Profile image"
                    />
                    <div className="text-xs text-gray-500">
                      {record.user.name}
                    </div>
                    <div className="w-1 h-1 bg-gray-500 rounded-full"></div>
                    <div className="text-xs text-gray-500">
                      {dateTimeFormatter(record.createdAt, "Jan 1, 2000")}
                    </div>
                    <div className="w-1 h-1 bg-gray-500 rounded-full"></div>
                    <div className="text-xs text-gray-500">{record.action}</div>
                    {record.undoParams &&
                      remainingUndoTime(record.createdAt) && (
                        <>
                          <div className="w-1 h-1 bg-gray-500 rounded-full"></div>
                          {undoingId.includes(record.undoParams) ? (
                            <SpinLoader size={28} />
                          ) : (
                            <div
                              className="px-2 text-xs textBlue bgLightBlue dark:bgBlack3 cursor-pointer rounded-full border hover:borderBlue hover:bg-white"
                              onClick={() =>
                                undoFunctions(record.undoParams, record.id)
                              }
                            >
                              Undo
                            </div>
                          )}
                          <div className="text-xs text-gray-500">
                            within {remainingUndoTime(record.createdAt)}
                          </div>
                        </>
                      )}
                  </div>
                </div>
              );
            })
          ))}
      </div>

      {showRefetchLoader === true && (
        <div className="w-full flex justify-center py-4">
          <SpinLoader />
        </div>
      )}
    </div>
  );
}
